___CSS_LOADER_EXPORT___.push([module.id, "/*\n *  Document   : formlayout.css\n *  Author     : RedStar Theme\n *  Description: This style sheet is used for format the bootstrap form components, you can\n \t\t\t\t change form style using edit this stylesheet.\n \n \t\t\n\t\t\t \t\t[Table of contents]\n\t\t\t \n\t\t\t \t\t1. FORM CONTROL\n\t\t\t\t\t2. FORM GROUP\n\t\t\t\t\t3. ICONS\t\n\t\t\t\t\t4. TOGGLE BUTTON\n\t\t\t\t\t5. CHECKBOX\n\t\t\t\t\t6. RADIO BUTTONS \n */\n\n/********* [1. FORM CONTROL]*********/\n\ninput:focus {\n  border: 1px solid silver !important;\n}\n\ntextarea:focus {\n  border: 1px solid silver !important;\n}\n\n.form-control {\n  border-radius: 10;\n  box-shadow: none;\n  border-color: silver;\n}\n\n.form-control:focus {\n  border-color: #3c8dbc;\n  /* box-shadow: none; */\n}\n\n.form-control::-moz-placeholder,\n.form-control:-ms-input-placeholder,\n.form-control::-webkit-input-placeholder {\n  color: #bbb;\n  opacity: 1;\n}\n\n.form-control:not(select) {\n  -webkit-appearance: none;\n  -moz-appearance: none;\n}\n\n/***************** [2. FORM GROUP] **************/\n\n.form-group.has-success label {\n  color: #00a65a;\n}\n\n.form-group.has-success .form-control,\n.form-group.has-success .input-group-addon {\n  border-color: #00a65a;\n  box-shadow: none;\n}\n\n.form-group.has-success .help-block {\n  color: #00a65a;\n}\n\n.form-group.has-warning label {\n  color: #f39c12;\n}\n\n.form-group.has-warning .form-control,\n.form-group.has-warning .input-group-addon {\n  border-color: #f39c12;\n  box-shadow: none;\n}\n\n.form-group.has-warning .help-block {\n  color: #f39c12;\n}\n\n.form-group.has-error label {\n  color: #dd4b39;\n}\n\n.form-group.has-error .form-control,\n.form-group.has-error .input-group-addon {\n  border-color: #dd4b39;\n  box-shadow: none;\n}\n\n.form-group.has-error .help-block {\n  color: #dd4b39;\n}\n\n/***************** [3. ICONS] **************/\n\n.input-icon > i {\n  color: #ccc;\n  display: block;\n  position: absolute;\n  margin: 11px 16px 4px 10px;\n  z-index: 3;\n  width: 16px;\n  font-size: 16px;\n  text-align: center;\n  left: 0;\n}\n\n.input-icon.right > i {\n  left: auto;\n  right: 8px;\n  float: right;\n}\n\n.has-success .input-icon > i {\n  color: #36c6d3;\n}\n\n.has-warning .input-icon > i {\n  color: #f1c40f;\n}\n\n.has-info .input-icon > i {\n  color: #659be0;\n}\n\n.has-error .input-icon > i {\n  color: #ed6b75;\n}\n\n.input-group .input-group-addon {\n  border-radius: 0;\n  border-color: #d2d6de;\n  background-color: #fff;\n  border: 1px solid #d4d0d0;\n  border-top: 1px solid #d4d0d0;\n  border-bottom: 1px solid #d4d0d0;\n}\n\n.input-group-addon {\n  padding: 6px 12px;\n}\n\n.btn-group-vertical .btn.btn-flat:first-of-type,\n.btn-group-vertical .btn.btn-flat:last-of-type {\n  border-radius: 0;\n}\n\n.input-group-sm > .form-control,\n.input-group-sm > .input-group-addon,\n.input-group-sm > .input-group-btn > .btn {\n  height: 30px;\n  padding: 5px 10px;\n  font-size: 12px;\n  line-height: 1.5;\n  border-radius: 3px;\n}\n\n.icheck > label {\n  padding-left: 0;\n}\n\n.form-control-feedback.fa {\n  line-height: 34px;\n}\n\n.input-lg + .form-control-feedback.fa,\n.input-group-lg + .form-control-feedback.fa,\n.form-group-lg .form-control + .form-control-feedback.fa {\n  line-height: 46px;\n}\n\n.input-sm + .form-control-feedback.fa,\n.input-group-sm + .form-control-feedback.fa,\n.form-group-sm .form-control + .form-control-feedback.fa {\n  line-height: 30px;\n}\n\n/************************************************* \n\n\t\t\t\t\tadvance form \n\t\t\t\n****************************************************/\n\n/*************** [4. TOGGLE BUTTON] ***************/\n\n.switchToggle {\n  position: relative;\n  display: inline-block;\n  width: 60px;\n  height: 34px;\n}\n\n.switchToggle input {\n  display: none;\n}\n\n.switchToggle .slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  margin: 0;\n  background-color: #ccc;\n  transition: 0.4s;\n}\n\n.switchToggle .slider:before {\n  position: absolute;\n  content: \"\";\n  height: 26px;\n  width: 26px;\n  left: 4px;\n  bottom: 4px;\n  background-color: white;\n  transition: 0.4s;\n}\n\n.switchToggle input:checked + .red {\n  background-color: #dc5754;\n}\n\n.switchToggle input:focus + .red {\n  box-shadow: 0 0 1px #dc5754;\n}\n\n.switchToggle input:checked + .green {\n  background-color: #64bd63;\n}\n\n.switchToggle input:focus + .green {\n  box-shadow: 0 0 1px #64bd63;\n}\n\n.switchToggle input:checked + .aqua {\n  background-color: #00c0ef;\n}\n\n.switchToggle input:focus + .aqua {\n  box-shadow: 0 0 1px #00c0ef;\n}\n\n.switchToggle input:checked + .yellow {\n  background-color: #f39c12;\n}\n\n.switchToggle input:focus + .yellow {\n  box-shadow: 0 0 1px #f39c12;\n}\n\n.switchToggle input:checked + .slider:before {\n  transform: translateX(26px);\n}\n\n.switchToggle .slider.round {\n  border-radius: 34px;\n}\n\n.switchToggle .slider.round:before {\n  border-radius: 50%;\n}\n\n/***************  [5. CHECKBOX] ***************/\n\n.checkbox {\n  padding-left: 20px;\n}\n\n.checkbox label {\n  display: inline-block;\n  position: relative;\n  padding-left: 5px;\n}\n\n.checkbox label::before {\n  content: \"\";\n  display: inline-block;\n  position: absolute;\n  width: 17px;\n  height: 17px;\n  left: 0;\n  margin-left: -20px;\n  border: 1px solid #cccccc;\n  background-color: #fff;\n  transition: border 0.15s ease-in-out, color 0.15s ease-in-out;\n}\n\n.checkbox label::after {\n  display: inline-block;\n  position: absolute;\n  width: 16px;\n  height: 16px;\n  left: 0;\n  top: 0;\n  margin-left: -20px;\n  padding-left: 3px;\n  padding-top: 1px;\n  font-size: 11px;\n  color: #555555;\n}\n\n.checkbox input[type=\"checkbox\"] {\n  opacity: 0;\n}\n\n.checkbox input[type=\"checkbox\"]:checked + label::after {\n  font-family: \"FontAwesome\";\n  content: \"\\f00c\";\n}\n\n.checkbox input[type=\"checkbox\"]:disabled + label {\n  opacity: 0.65;\n}\n\n.checkbox input[type=\"checkbox\"]:disabled + label::before {\n  background-color: #eeeeee;\n  color: #eeeeef;\n  cursor: not-allowed;\n}\n\n.checkbox.checkbox-inline {\n  margin-top: 0;\n}\n\n.checkbox-aqua input[type=\"checkbox\"]:checked + label::before {\n  background-color: #00c0ef;\n  border-color: #00c0ee;\n}\n\n.checkbox-aqua input[type=\"checkbox\"]:checked + label::after {\n  color: #fff;\n}\n\n.checkbox-aqua input[type=\"checkbox\"]:hover + label::before {\n  border: 2px solid #00c0ef;\n}\n\n/* checkbox with icon color */\n\n.checkbox-icon-aqua input[type=\"checkbox\"]:checked + label::after {\n  color: #00c0ef;\n}\n\n.checkbox-icon-aqua input[type=\"checkbox\"]:hover + label::before {\n  border: 1px solid #00c0ef;\n}\n\n/* checkbox with backgound color */\n\n.checkbox-yellow input[type=\"checkbox\"]:checked + label::before {\n  background-color: #f39c12;\n  border-color: #f39c11;\n}\n\n.checkbox-yellow input[type=\"checkbox\"]:checked + label::after {\n  color: #fff;\n}\n\n.checkbox-yellow input[type=\"checkbox\"]:hover + label::before {\n  border: 2px solid #f39c12;\n}\n\n/* checkbox with icon color */\n\n.checkbox-icon-yellow input[type=\"checkbox\"]:checked + label::after {\n  color: #f39c12;\n}\n\n.checkbox-icon-yellow input[type=\"checkbox\"]:hover + label::before {\n  border: 1px solid #f39c12;\n}\n\n.checkbox-black input[type=\"checkbox\"]:checked + label::before {\n  background-color: #000000;\n  border-color: #000009;\n}\n\n.checkbox-black input[type=\"checkbox\"]:checked + label::after {\n  color: #fff;\n}\n\n.checkbox-black input[type=\"checkbox\"]:hover + label::before {\n  border: 2px solid #000000;\n}\n\n.checkbox-icon-black input[type=\"checkbox\"]:checked + label::after {\n  color: #000000;\n}\n\n.checkbox-icon-black input[type=\"checkbox\"]:hover + label::before {\n  border: 1px solid #000000;\n}\n\n.checkbox-red input[type=\"checkbox\"]:checked + label::before {\n  background-color: #dc5754;\n  border-color: #dc5755;\n}\n\n.checkbox-red input[type=\"checkbox\"]:checked + label::after {\n  color: #fff;\n}\n\n.checkbox-red input[type=\"checkbox\"]:hover + label::before {\n  border: 2px solid #dc5754;\n}\n\n.checkbox-icon-red input[type=\"checkbox\"]:checked + label::after {\n  color: #dc5754;\n}\n\n.checkbox-icon-red input[type=\"checkbox\"]:hover + label::before {\n  border: 1px solid #dc5754;\n}\n\n/***************  [6. RADIO BUTTONS ] ***************/\n\n.radio {\n  padding-left: 20px;\n}\n\n.radio label {\n  display: inline-block;\n  position: relative;\n  padding-left: 5px;\n}\n\n.radio label::before {\n  content: \"\";\n  display: inline-block;\n  position: absolute;\n  width: 17px;\n  height: 17px;\n  left: 0;\n  margin-left: -20px;\n  border: 1px solid #cccccc;\n  border-radius: 50%;\n  background-color: #fff;\n  transition: border 0.15s ease-in-out;\n}\n\n.radio label::after {\n  display: inline-block;\n  position: absolute;\n  content: \" \";\n  width: 11px;\n  height: 11px;\n  left: 3px;\n  top: 3px;\n  margin-left: -20px;\n  border-radius: 50%;\n  background-color: #555555;\n  transform: scale(0, 0);\n  transition: transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);\n}\n\n.radio input[type=\"radio\"] {\n  opacity: 0;\n}\n\n.radio input[type=\"radio\"]:checked + label::after {\n  transform: scale(1, 1);\n}\n\n.radio input[type=\"radio\"]:disabled + label {\n  opacity: 0.65;\n}\n\n.radio input[type=\"radio\"]:disabled + label::before {\n  cursor: not-allowed;\n}\n\n.radio.radio-inline {\n  margin-top: 0;\n}\n\n.radio-black input[type=\"radio\"] + label::after {\n  background-color: #000000;\n}\n\n.radio-black input[type=\"radio\"]:checked + label::before {\n  border-color: #000000;\n}\n\n.radio-black input[type=\"radio\"]:checked + label::after {\n  background-color: #000000;\n}\n\n.radio-yellow input[type=\"radio\"] + label::after {\n  background-color: #f39c12;\n}\n\n.radio-yellow input[type=\"radio\"]:checked + label::before {\n  border-color: #f39c12;\n}\n\n.radio-yellow input[type=\"radio\"]:checked + label::after {\n  background-color: #f39c12;\n}\n\n.radio-red input[type=\"radio\"] + label::after {\n  background-color: #dc5754;\n}\n\n.radio-red input[type=\"radio\"]:checked + label::before {\n  border-color: #dc5754;\n}\n\n.radio-red input[type=\"radio\"]:checked + label::after {\n  background-color: #dc5754;\n}\n\n.radio-aqua input[type=\"radio\"] + label::after {\n  background-color: #00c0ef;\n}\n\n.radio-aqua input[type=\"radio\"]:checked + label::before {\n  border-color: #00c0ef;\n}\n\n.radio-aqua input[type=\"radio\"]:checked + label::after {\n  background-color: #00c0ef;\n}\n\n.radioicon-group {\n  margin-top: 10px;\n}\n\n.radioicon label {\n  font-weight: normal;\n  font-size: 14px;\n  color: black;\n  margin: 0px;\n  height: 25px;\n}\n\n.radioicon span {\n  font-size: 19px;\n  height: 25px;\n  width: 20px;\n}\n\n/* Hidding the radiobuttons & checkboxes */\n\n.radioicon input[type=\"radio\"] {\n  display: none;\n}\n\n/* Hidding the \"check\" status of inputs */\n\n.radioicon input[type=\"radio\"] + label .fa-circle {\n  display: block;\n  color: #cccccc;\n}\n\n/* Styling the \"check\" status */\n\n.radioicon-aqua input[type=\"radio\"]:checked + label .fa-circle {\n  display: block;\n  color: #00c0ef;\n}\n\n.radioicon-red input[type=\"radio\"]:checked + label .fa-circle {\n  display: block;\n  color: #dc5754;\n}\n\n.radioicon-yellow input[type=\"radio\"]:checked + label .fa-circle {\n  display: block;\n  color: #f39c12;\n}\n\n.radioicon-black input[type=\"radio\"]:checked + label .fa-circle {\n  display: block;\n  color: #000000;\n}\n\n/* Spinner control */\n\n.input-group-btn-vertical {\n  position: relative;\n  white-space: nowrap;\n  width: 2%;\n  vertical-align: middle;\n  display: table-cell;\n}\n\n.input-group-btn-vertical > .btn {\n  display: block;\n  float: none;\n  width: 30px;\n  padding: 8px;\n  margin-left: -1px;\n  position: relative;\n  border-radius: 0;\n}\n\n/* .input-group-btn-vertical > .btn:first-child {\n  border-top-right-radi